.NBSuper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color2);
}

.NBContainer {
  font-family: "Roboto Mono", monospace;
  color: var(--color1);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin-top: 20px;

  /* border: solid 1px lime; */
}

.NBLogo img {
  height: 90px;
}

.NBMenu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 70%;

  /* border: solid 1px red; */
}

.NBMenu a {
  transition: 0.5s;
}

.NBMenu a:hover {
  transform: scale(1.2);
}

.NBSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 10%;
}
