* {
  cursor: none;
}

.HHMain {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color2);
  /* height: 70vh; */
  font-family: "Sixtyfour", sans-serif;
}

.HHCont1 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: start;
  width: 50%;
  margin-top: 200px;
}

.HHMain h3 {
  font-size: 38px;
  margin: 10px;
  color: var(--color1);
}

.HHMain h4 {
  font-size: 18px;
  color: var(--color4);
  margin-top: 70px;
}

.Cursor {
  background-color: var(--color1);
  height: 32px;
  width: 32px;
  border-radius: 50%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 17;
}

.HHElement1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color2);
  height: 120px;
  width: 300px;
  border-radius: 50%;
  margin-top: 100px;
  color: var(--color2);

  /* border: solid red 1px; */
}

.HHElement1 p {
  font-size: 40px;
  transition: 0.5s;
  margin: 0px;
  padding: 0px;
  transition: 0.5s;
}

.HHElement1:hover {
  color: var(--color3);
}

.Boing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color3);
  height: 10px;
  width: 10px;
  border-radius: 50%;
  animation: boing 1s linear infinite;
}

.NoBoing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--color3);
  height: 10px;
  width: 10px;
  transform: translateY(25px);
  border-radius: 50%;
  animation: boing 1s linear;
}

@keyframes boing {
  0% {
    transform: translateY(0px) translateX(0px);
  }
  20% {
    transform: translateY(8px) translateX(6px);
  }
  25% {
    transform: translateY(16px) translateX(7px);
  }
  50% {
    transform: translateY(0px) translateX(0px);
  }
  70% {
    transform: translateY(8px) translateX(-6px);
  }
  75% {
    transform: translateY(16px) translateX(-7px);
  }
}

.Chapter {
  height: 70vh;
  width: 70%;

  margin-top: 50px;
}

.Chapter1 {
  background-color: var(--color3);
}

.Chapter2 {
  background-color: var(--color4);
}

.Chapter3 {
  background-color: var(--color2);
  border: 2px solid var(--color3);
}

.Chapter h1,
.Chapter p {
  font-family: "Roboto Mono", monospace;
  /* background-color: var(--color3); */
  color: var(--color2);
  font-size: 24px;
  margin: 10px;
}

.Chapter span {
  font-weight: bold;
}

.Secuencia {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
  height: 40vh;

  /* border: rebeccapurple solid 1px; */
}

.SecuenciaSub {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: relative;
  height: 100%;
  width: 100%;

  /* border: rebeccapurple solid 1px; */
}

.SecuenciaSub2 {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: relative;
  height: 100%;
  width: 100%;

  /* border: rebeccapurple solid 1px; */
}

.SecBtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* position: absolute; */
  height: 100px;
  width: 100px;
  border-radius: 50%;
  z-index: 10;
  background-color: var(--color2);
  border: solid var(--color3) 2px;

  /* border: rgb(32, 109, 32) solid 1px; */
}

.SecBtn p {
  color: var(--color3);
}

.SecBtnHappy {
  background-color: var(--color1);
  border: solid var(--color2) 2px;
}

.SecBtnHappy p {
  color: var(--color2);
}

.SecCont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 200px;
  width: 200px;
  border-radius: 3rem;
  background-color: var(--color2);
}

.SecCont p {
  color: var(--color3);
}

.HHHouses {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
}

.HHHouse {
  display: flex;
  /* **flex: 1; */
  flex-direction: column;
  justify-content: center;
  align-items: center;

  /* border: solid lime 1px; */
}

.HHup p,
.HHdown p {
  font-family: "Courier Prime", monospace;
  font-size: 16px;
  margin: 0px;
  font-weight: bolder;
  user-select: none;
}

.HHdown,
.HHup {
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  height: 44px;
  width: 260px;

  /* border: solid rgb(255, 247, 0) 1px; */
}

.HHup {
  justify-content: flex-end;
}

.HHmid img {
  height: 260px;
  width: 260px;
  z-index: 8;
  pointer-events: none;
  user-select: none;
}

.HHmidAlt {
  filter: brightness(70%);
}

.HHWindow img {
  display: flex;
  /* position: absolute; */
  /* top: 105px; */
  /* left: 290px; */
  height: 55px;
  width: 47px;
  z-index: 16;
  pointer-events: auto;
}

.FatBtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  z-index: 10;
  background-color: var(--color1);
  border: solid var(--color2) 2px;
  user-select: none;
}

.FatBtn p {
  user-select: none;
}

.Chapter3 h1,
.Chapter3 p {
  color: var(--color3);
}

.HHSpark {
  width: 200px;
  border-radius: 50%;

  /* border: 1px solid red; */
}
